// Для импорта в диспетчер подключений: @import './src/less/blocks/content.less';


.content {
	width: 100%;
	position: relative;
	padding-bottom: 60px;

	&:after {
		content: '';
		display: inline-block;
		width: 80%;
		height: 3px;
		background-color: @dark-green-color;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}
	&--about:after {
		display: none;
	}
	&--article:after {
		display: none;
	}

	&--article {
		text-align: center;
	}

	&--article .content__img {
		width: auto;
		height: auto;
		margin: 30px auto;
	}

	&__img-wrapper {
		width: 100%;
		position: relative;
	}
	&__img {
		width: 100%;
		height: auto;
	}
	&__text {
		color: @dark-green-color;
		font-size: 1.0em;
		text-align: justify;
	}
	p {
		color: @dark-green-color;
		font-size: 1.0em;
		text-align: justify;
	}
	h1 {
		color: @dark-green-color;
		font-size: 35px;
		font-weight: normal;
		text-align: center;
		padding: 20px 0;
		width: 100%;

		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			font-size: 24px;  
		}
	}

	h2 {
		color: @dark-green-color;
		font-size: 25px;
		font-weight: normal;
		text-align: left;
		padding: 10px 0;
		width: 100%;

		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			font-size: 18px;  
		}
	}
	&__img-team {
		width: 190px;
		height: 190px;
		margin: 20px 10px;
	}
	&__text-team {
		display: inline-block;
		width: 70%;
		margin: 20px 10px;
		text-align: justify;
	}
	&__team-wrapper {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		-webkit-flex-direction: row;
		-moz-flex-direction: row;
		-ms-flex-direction: row;
		-o-flex-direction: row;
		flex-direction: row;
		-webkit-flex-wrap: wrap;
		-moz-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		-o-flex-wrap: wrap;
		flex-wrap: wrap;
		justify-content: center;
	}
	&__btn {
		height: 53px;
		display: inline-block;
		text-align: center;
		text-decoration: none;
		background-color: #4b6856;
		color: #b0c7a6;
		font-size: 1.3em;
		line-height: 53px;
		width: 190px;
		position: absolute;
		bottom: 10px;
		right: 10px;
	}

}
