// Для импорта в диспетчер подключений: @import './src/less/blocks/magazine.less';


.magazine {

	&__block {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;

		&--books {
			justify-content: space-around;
		}
	}
	&__item {
		width: 160px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom: 10px;
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 130px;
			margin-bottom: 10px;
		}
	}
	&__books-item {
		width: 240px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-bottom: 15px;

		.magazine__img-wrapper {
			width: 240px;
			height: 390px;
			display: inline-block;
			position: relative;
			overflow: hidden;
		}
		.magazine__text {
			height: auto;
			margin: 10px 0;
			line-height: 1.2;
			text-align: center;
		}
	}
	&__list {
		margin-bottom: 30px;
	}
	&__img-wrapper {
		display: inline-block;
		width: 160px;
		height: 260px;
		position: relative;
		overflow: hidden;
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 130px;
			height: 230px;
		}
	}
	&__img {
		width: auto;
		height: 100%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}
	&__text {
		font-size: 25px;
		color: @dark-green-color;
		line-height: 50px;
		display: inline-block;
		height: 50px;
	}
	&__subtext {
		font-size: 16px;
		color: @dark-green-color;
		display: inline-block;
		line-height: 1.3;
		margin-bottom: 15px;
	}
	&__btn {
		height: 48px;
		display: inline-block;
		text-align: center;
		text-decoration: none;
		background-color: @green-color;
		color: @light-green-color;
		font-size: 1.3em;
		line-height: 48px;
		width: 100%;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			font-size: 16px;
			height: 23px;
			line-height: 23px;
			span {
				display: none;
			}
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			font-size: 16px;
			height: 23px;
			line-height: 23px;
			width: 100%;
		}
	}

}

.book__info {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
}
.book__img-wrapper {
	width: 320px;
	height: 430px;
	margin-right: 50px;
	position: relative;
	display: inline-block;
	overflow: hidden;
}
.book__img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	height: 100%;
	width: auto;
}
.book__text {
	width: 610px;
}
.book__text p {
	color: @dark-green-color;
	font-size: 16px;
	text-align: justify;
}
.book__text h2 {
	color: @dark-green-color;
	font-size: 16px;
	text-align: left;
	font-weight: 600;
	margin-top: 50px;
}
@media (min-width: @screen-xs) and (max-width: @screen-sm) {
	.book__info {
		flex-wrap: wrap;
	}
	.book__img-wrapper {
		width: 100%;
		margin-right: 0;
		margin-bottom: 30px;
	}
	.book__text {
		width: 100%;
	}
}