// Для импорта в диспетчер подключений: @import './src/less/blocks/menu.less';


.menu {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-direction: column;
	-moz-flex-direction: column;
	-ms-flex-direction: column;
	-o-flex-direction: column;
	flex-direction: column;
	position: relative;

	&:after {
		content: '';
		display: none;
		width: 300%;
		height: 100%;
		background-color: @light-light-green-color;
		position: absolute;
		top: 0;
		z-index: -1;
		left: 50%;
		transform: translateX(-50%);

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			display: inline-block;
		}
	}

	@media (min-width: @screen-sm) and (max-width: @screen-lg) {
		-webkit-flex-direction: row;
		-moz-flex-direction: row;
		-ms-flex-direction: row;
		-o-flex-direction: row;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
		background-color: @light-light-green-color;
	}

	&__item {
		list-style-type: none;
		flex-grow: 1;
		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			border-left: 1px solid @light-green-color;
		}
		&:last-of-type {
			border-right: 1px solid @light-green-color;
		}
	}
	&__link {
		font-size: 1em;
		color: @dark-green-color;
		padding: 0 10px;
		line-height: 53px;
		height: 53px;
		display: inline-block;
		text-align: center;
		margin-bottom: 5px;
		font-weight: bold;
		white-space: nowrap;
		width: 100%;
		text-decoration: none;
		background-color: @light-green-color;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			padding: 0;
			background-color: transparent;
			font-weight: normal;
			height: 50px;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			line-height: 40px;
			height: 40px;
			margin-bottom: 0;
		}
	}
}
