// Для импорта в диспетчер подключений: @import './src/less/blocks/search.less';


.search {
	background-color: @green-color;
	padding: 5px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-direction: row;
	-moz-flex-direction: row;
	-ms-flex-direction: row;
	-o-flex-direction: row;
	flex-direction: row;
	-ms-align-items: center;
	align-items: center;
	margin-bottom: 10px;
	margin-left: 10%;

	@media (min-width: @screen-sm) and (max-width: @screen-lg) {
		margin-left: 5%;
	}
	@media (min-width: @screen-xs) and (max-width: @screen-sm) {
		display: none;
		position: absolute;
		width: 100%;
		left: 0;
		margin: 0;
		top: 100%;
		z-index: 99;
		height: 40px;
	}

	&__input {
		border: 0;
		height: 21px;
		font-size: 0.8em;
		padding-left: 5px;
		outline: none;
		
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 86%;
			height: 30px;
			font-size: 1.0em;
			position: absolute;
			top: 5px;
			left: 5px;
		}
	}

	&__btn {
		width: 23px;
		height: 23px;
		background-image: url('/img/icon_search.png');
		background-color: #fff;
		display: inline-block;
		text-decoration: none;
		-webkit-background-size: contain;
		background-size: contain;
		background-repeat: no-repeat;
		margin-left: 5px;
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			height: 30px;
			width: 30px;
			-webkit-background-size: cover;
			background-size: cover;
			position: absolute;
			top: 5px;
			right: 5px;
		}
	}

	&__btn-header {
		background-image: url('/img/icon_search_bd0.png');
		display: inline-block;
		text-decoration: none;
		background-repeat: no-repeat;
		margin-left: 5px;
		display: none;
		cursor: pointer;
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			height: 30px;
			width: 30px;
			-webkit-background-size: cover;
			background-size: cover;
			display: block;
		}
	}
}
