// Для импорта в диспетчер подключений: @import './src/less/blocks/project.less';


.project {
	&__content {
		display: flex;
		flex-direction: column;

		p {
			font-size: 16px;
			color: @dark-green-color;
			text-align: justify;
			line-height: 1.2;
			margin-bottom: 10px;
		}
		img {
			margin: 20px auto;

			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				width: 100%;
			}
		}
	}
	&__gallery {
		padding: 0 120px;
		margin-bottom: 50px;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			padding: 0 65px;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			padding: 0 25px;
		}

		.slick-arrow {
			font-size: 0;
			background-color: transparent;
			border: 0;
			display: inline-block;
			width: 30px;
			height: 58px;
			background-size: contain;
			background-repeat: no-repeat;
			position: absolute;
			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				width: 25px;
				height: 40px;
			}
			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				width: 15px;
				height: 30px;
			}
		}
		.slick-next {
			background-image: url('/img/icon_arrow_gallery_next.png');
			right: 10px;

			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				right: 20px;
			}

			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				right: 0px;
			}
		}
		.slick-prev {
			background-image: url('/img/icon_arrow_gallery.png');
			left: 10px;

			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				left: 20px;
			}

			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				left: 0px;
			}
		}
		.slick-prev:before, .slick-next:before {
			display: none;
		}
	}
}
