* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
h1 {
  margin: 0;
}
body {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  font-size: 16px;
  position: relative;
  font-family: 'Rubik', sans-serif;
  transition: 0.3s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  padding: 0 10px;
  margin: 0 auto;
  width: @screen-lg;
  box-sizing: border-box;
  @media(min-width: @screen-xs) {
    width: @screen-xs;
  }
  @media(min-width: @screen-sm) {
    width: @screen-sm;
  }
  @media(min-width: @screen-lg) {
    width: @screen-lg;
  }
}

.section-title {
  color: @dark-green-color;
  font-size: 35px;
  font-weight: normal;
  text-align: center;
  padding: 20px 0;
  width: 100%;

  @media (min-width: @screen-xs) and (max-width: @screen-sm) {
    font-size: 24px;  
  }
}

.section-subtitle {
  color: @dark-green-color;
  font-size: 25px;
  font-weight: normal;
  text-align: left;
  padding: 10px 0;
  width: 100%;

  @media (min-width: @screen-xs) and (max-width: @screen-sm) {
    font-size: 18px;  
  }
}