// Для импорта в диспетчер подключений: @import './src/less/blocks/logo.less';


.logo {
	@media (min-width: @screen-xs) and (max-width: @screen-sm) {
		width: 50%;
	}
	@media (min-width: @screen-xs) and (max-width: @screen-sm) {
		width: 75%;
	}
	&__img {
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 100%;
		}
	}
}
