// Для импорта в диспетчер подключений: @import './src/less/blocks/sidebar.less';


.sidebar {
	width: 25%;
	display: block;

	@media (min-width: @screen-sm) and (max-width: @screen-lg) {
		width: 100%;
	}
	@media (min-width: @screen-xs) and (max-width: @screen-sm) {
		width: 100%;
		display: none;
	}
}
