// Для импорта в диспетчер подключений: @import './src/less/blocks/feedback.less';


.feedback {
	padding: 0 90px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-direction: row;
	-moz-flex-direction: row;
	-ms-flex-direction: row;
	-o-flex-direction: row;
	flex-direction: row;
	-webkit-flex-wrap: wrap;
	-moz-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	-o-flex-wrap: wrap;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-left: -50px;

	@media (min-width: @screen-sm) and (max-width: @screen-lg) {
		padding: 0 10%;
	}
	@media (min-width: @screen-xs) and (max-width: @screen-sm) {
		margin: 0;
		padding: 0;
	}

	&__map {
		width: 420px;
		height: 300px;
		display: inline-block;
		background-image: url('/img/map.png');
		border: 1px solid @dark-green-color;
		background-position: center;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			width: 228px;
			height: 200px;
		}
	}
	&__form {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
		flex-basis: 20%;

		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			margin: 20px 0;
			width: 100%;
			flex-basis: 100%;
		}
	}
	&__input {
		height: 30px;
		line-height: 29px;
		font-size: 1.0em;
		color: @green-color;
		border: 1px solid @dark-green-color;
		box-shadow: inset 0px 3px 3px 0px rgba(0, 0, 0, 0.33);
		margin-bottom: 10px;
		padding-left: 10px;
		outline: none;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			height: 25px;
			line-height: 24px;
			margin-bottom: 5px;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 100%;
		}

		&--big {
			height: 50px;

			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				height: 35px;
			}
		}
	}
	&__text {
		font-size: 0.7em;
		margin: 10px 0;
		line-height: 16px;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			line-height: 14px;
			margin: 5px 0;
			margin-top: 0;
		}
	}
	&__btn {
		height: 53px;
		display: inline-block;
		text-align: center;
		text-decoration: none;
		background-color: @green-color;
		color: @light-green-color;
		font-size: 1.3em;
		line-height: 53px;
		width: 100%;
		margin-top: 15px;
		border: 0;
		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			font-size: 16px;
			height: 30px;
			line-height: 30px;
			margin-top: 5px;
		}
	}
}
