// Для импорта в диспетчер подключений: @import './src/less/blocks/copyright.less';


.copyright {
  margin-left: auto;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -webkit-flex-direction: row;
  -moz-flex-direction: row;
  -ms-flex-direction: row;
  -o-flex-direction: row;
  flex-direction: row;
  -ms-align-items: flex-start;
  align-items: flex-start;

  &__img {
  	width: 50px;
  	height: auto;
  }

  &__text {
  	text-align: right;
  	margin-left: 15px;
  	font-size: 0.6em;
  	color: @dark-green-color;
  }
}
