// Для импорта в диспетчер подключений: @import './src/less/blocks/link.less';


.link {
	position: relative;
	padding-bottom: 50px;

	&:after {
		content: '';
		display: inline-block;
		width: 80%;
		height: 3px;
		background-color: @dark-green-color;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}
	&--about:after {
		display: none;
	}
	&--about-page .link__item:first-of-type {
		-webkit-flex-direction: row-reverse;
		-moz-flex-direction: row-reverse;
		-ms-flex-direction: row-reverse;
		-o-flex-direction: row-reverse;
		flex-direction: row-reverse;
	}
	&--about-page .link__item-box {
		margin: 0 30px;		
	}

	&__item-box {
		width: 60%;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	&__item-box .link__btn {
		width: 220px;
		margin-right: auto;
	}

	&__item {
		padding: 10px 20px;
		border: 2px solid @dark-green-color;
		margin: 20px 30px;
		width: 376px;
		display: block;

		&--big {
			border: 0;
			width: 100%;
			padding: 0;

			.link__img-wrapper {
				width: 266px;
				height: 200px;
				overflow: hidden;
			}
		}

		@media (min-width: @screen-lg) {
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex !important;
			-webkit-flex-direction: row;
			-moz-flex-direction: row;
			-ms-flex-direction: row;
			-o-flex-direction: row;
			flex-direction: row;
			-webkit-flex-wrap: wrap;
			-moz-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			-o-flex-wrap: wrap;
			flex-wrap: wrap;
		}

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			width: 120px;
			margin: 20px;
			text-align: center;
			justify-content: center;
			padding: 10px;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			width: 120px;
			margin: 1% 2%;
			padding: 10px;
			text-align: center;
		}

	}
	&__title {
		font-size: 25px;
		color: @dark-green-color;
		font-weight: normal;
		margin-bottom: 15px;
		width: 100%;

		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			font-size: 16px;
		}
	}
	&__img-wrapper {
		border: 2px solid @light-green-color;
		position: relative;
		width: 110px;
		height: 110px;
		padding: 1px;
		margin-right: 20px;
		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			margin: 0 auto;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			margin: 0 auto;
			width: 74px;
			height: 74px;
		}
	}
	&__wrapper-about {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		-webkit-flex-direction: row;
		-moz-flex-direction: row;
		-ms-flex-direction: row;
		-o-flex-direction: row;
		flex-direction: row;
		justify-content: space-around;

		.link__item {
			-webkit-flex-direction: column;
			-moz-flex-direction: column;
			-ms-flex-direction: column;
			-o-flex-direction: column;
			flex-direction: column;
			-ms-align-items: center;
			align-items: center;
			justify-content: center;
			border: 0;
		}
	}
	&__wrapper {

		@media (min-width: @screen-lg) {
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			-webkit-flex-direction: row;
			-moz-flex-direction: row;
			-ms-flex-direction: row;
			-o-flex-direction: row;
			flex-direction: row;
			-webkit-flex-wrap: wrap;
			-moz-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			-o-flex-wrap: wrap;
			flex-wrap: wrap;
			justify-content: space-around;
		}

		.slick-arrow {
			font-size: 0;
			background-color: transparent;
			border: 0;
			display: inline-block;
			width: 30px;
			height: 58px;
			background-size: contain;
			background-repeat: no-repeat;
			position: absolute;
			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				width: 25px;
				height: 40px;
			}
			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				width: 15px;
				height: 30px;
			}
		}
		.slick-next {
			background-image: url('/img/icon_arrow_gallery_next.png');
			right: -50px;

			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				right: -20px;
			}

			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				right: -10px;
			}
		}
		.slick-prev {
			background-image: url('/img/icon_arrow_gallery.png');
			left: -50px;

			@media (min-width: @screen-sm) and (max-width: @screen-lg) {
				left: -20px;
			}

			@media (min-width: @screen-xs) and (max-width: @screen-sm) {
				left: -10px;
			}
		}
		.slick-prev:before, .slick-next:before {
			display: none;
		}
	}
	&__img {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%,-50%);
	}
	&__text {
		display: inline-block;
		flex-basis: 53%;
		text-align: justify;
		font-size: 0.8em;
		color: @dark-green-color;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			display: none;
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			display: none;
		}
	}
	&__btn {
		height: 53px;
		display: inline-block;
		text-align: center;
		text-decoration: none;
		background-color: @green-color;
		color: @light-green-color;
		font-size: 1.3em;
		line-height: 53px;
		width: 100%;
		margin-top: 15px;

		@media (min-width: @screen-sm) and (max-width: @screen-lg) {
			font-size: 16px;
			height: 23px;
			line-height: 23px;
			span {
				display: none;
			}
		}
		@media (min-width: @screen-xs) and (max-width: @screen-sm) {
			font-size: 16px;
			height: 23px;
			line-height: 23px;
			span {
				display: none;
			}
		}
	}

}
