// Для импорта в диспетчер подключений: @import './src/less/blocks/page-footer.less';


.page-footer {
	padding: 10px 0;
	background-color: @light-green-color;

	.container {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		-webkit-flex-direction: row;
		-moz-flex-direction: row;
		-ms-flex-direction: row;
		-o-flex-direction: row;
		flex-direction: row;
		justify-content: space-between;
		-ms-align-items: flex-start;
		align-items: flex-start;
	}
  
}
